.cookie-banner {
	position: fixed;
	padding: 24px;
	bottom: -500px;
	display: grid;
	grid-template-columns: auto auto;
	grid-gap: 24px;
	width: 100%;
	align-items: center;
	transition: all 3s linear;
	
	&--visible {
		bottom: -0px;
	}

}
.footer {
	background-color: $black;
	
	&__inner {
		min-height: 390px;
		padding: 30px;
		max-width: $max-width;
		margin: 0 auto;
	}

	&__info-block-wrapper {
		display: grid;
		grid-template: auto / repeat(3, 1fr);
		grid-template-areas:
			"block-1 block-2 block-3";
		grid-gap: 40px;
		padding-bottom: 60px;
		margin-bottom: 30px;
		border-bottom: 1px solid $white;
	}

	&__info-block {

		&--block-1 { grid-area: block-1 }
		&--block-2 { grid-area: block-2 }
		&--block-3 { grid-area: block-3 }

		p, h3 {
			color: $white;
		}

	}

	&__text-item {
		display: block;
		margin-bottom: 7.5px;
	}

	&__newsletter-subscription {
		margin-bottom: 30px;
	}

	&__image {
		margin-bottom: 30px;
		max-width: 100%
	}

	&__link {
		display: block;
	}

	&__social-media-icons {
		margin-bottom: 30px;
	}

	&__language-button {
		padding: 5px 10px;
		margin-right: 15px;
	}

	&__copyright {
		text-align: end;
		color: $white;
	}

	@media (max-width: 750px) {
		&__info-block-wrapper {
			grid-template: auto auto / repeat(2, 1fr);
			grid-template-areas:
				"block-1 block-2"
				"block-3 block-3";
		}
	}
	
	@media (max-width: 500px) {
		&__info-block-wrapper {
			grid-template: auto auto auto / repeat(1, 1fr);
			grid-template-areas:
				"block-1" 
				"block-2"
				"block-3";
		}

	}

}
.navigation-desktop {

		z-index: 10;
		border-bottom: 1px solid $black;
		z-index: 10;
		position: fixed;
		top: 0;
		right: 0;
		left: 0;
		height: 100px;
		background-color: white;
		
		&__inner {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
			padding: 0 30px;
			max-width: 1500px;
			margin: 0 auto;
			height: 100px;
		}

		&__logo-link {
			line-height: 1;
		}
		
		&__logo {
			height: 50px;
		}

		&__link {
			position: relative;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding-right: 40px;
			
			&--has-childs {

				&:hover .navigation-desktop__dropdown, 
				+ .navigation-desktop__dropdown:hover {
					display: flex;
				}

				.link__item {
		
						&:after {
							content: url('/media/icons/general/carret.svg');
							width: 8px;
							position: absolute;
							height: 8px;
							display: inline-block;
							margin-left: 8px;
							transform: translateY(-25%);
						}
					
					}


			}

		}

		&__links {
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
		}
		
		&__item {
			padding: 8px;
			display: inline-block;
			line-height: 1;
		
		}

		&__dropdown, .dropdown {
			display: none;
			position: absolute;
			top: 100px;
			left: 0;
			flex-direction: column;
			justify-content: flex-start;
			align-items: flex-start;
			background-color: $white;
			border: 1px solid $black;
			border-top: 0;
			transition-delay: 3s;
			
			&__item {
				padding: 8px 16px;
				width: 100%;
				
				&:hover {
					background-color: $grey;
				}
				
			}
		
		}
		
	}
	
	.navigation-desktop__placeholder {
		margin-bottom: 100px;
	}
	
	@media (max-width: 1040px) {
		
		.navigation-desktop, 
		.navigation-desktop__placeholder {
			display: none;
		}

}
	.navigation-mobile {
		display: none;
		z-index: 10;
		position: fixed;
		top: 0;
		right: 0;
		left: 0;
		height: 75px;
		background-color: $white;

		&__inner {
			max-width: $max-width;
			margin: 0 auto;
		}

		&__background-layer {
			background-color: $black;
			opacity: 0;
			position: fixed;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: -1;
			transition: all 0.3s ease;
			touch-action: none;
			pointer-events: none;
			
			&--active {
				opacity: 0.75;
			}

		}
		
		&__bar {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
			padding: 16px;
			height: 75px;
			border-bottom: 1px solid $black;
			background-color: $white;
		}

		&__panel {
			background-color: $grey;
