			height: 100vh;
			max-width: 500px;
			display: flex;
			flex-direction: column;
			align-items: flex-end;
			position: absolute;
			right: -100%;
			transition: all 0.3s ease;
			overflow: scroll;
			border-left: 1px solid $black;

			&--open {
				right: -0%;
			}

		}

		&__logo-link {
