			align-items: flex-end;
			background-color: $white;
			border-bottom: 1px solid $black;
			
			&--open {
				max-height: unset;
				display: flex;
				width: 100%;
			}
			
			&__item {
				color: $black;
				padding: 16px 16px 16px 48px;
				width: 100%;
				border-bottom: 1px solid $black;

				&:last-child {
					border-bottom: none;
				}
		
		}
		
	}
	
}
	
	.navigation-mobile__placeholder {
		margin-bottom: 75px;
	}
	
	@media (max-width: 1040px) {
		
		.navigation-mobile, 
		.navigation-mobile__placeholder {
			display: block
		}
		
	}

.checkbox {
	
	&__option {
		display: inline-flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		margin-right: 30px;
		margin-bottom: 15px;
	}

	&__option-wrapper:last-child {
		margin-right: 0px;
	}

	&__input {
		width: 24px;
    height: 24px;
		background-color: $input-background-color;
    appearance: none;
    vertical-align: bottom;
    margin-right: 16px;
    background-image: url('/media/icons/form-elements/checkbox-check.svg');
    background-size: 0px;
    background-position: center;
    transition: background-size 0.1s ease;
    background-repeat: no-repeat;
		border: $input-border;

		&:checked {
			background-size: 16px;
			background-color: $black;
		}

	}

	&__label {
		margin-top: 5px;
	}

}
.dropdown {

	&__input {
		background-color: $input-background-color;
		border-radius: $input-border-radius;
		padding: $input-padding;
		outline: $input-outline;
		margin: $input-margin;
		width: $input-width;
		border: $input-border;
		display: block;
		position: relative;

		&::after {
			content: "";
			width: 12px;
			height: 6px;
			position: absolute;
			right: 12px;
			top: 18px;
			background: url('/media/icons/form-elements/dropdown-carret.svg') no-repeat;
		}
	}

	&__checkbox {
		display: none;

		&:checked + .dropdown__options {
			display: flex;
		}
		
	}
	
	&__options {
		display: none;
		border: $input-border;
		border-radius: $input-border-radius;
		flex-direction: column;
		overflow: hidden;
	}

	&__option, .option {
		
		&__input {
			border: $input-border;
			display: none;
		}

		&__label {
			display: block;
			padding: $input-padding;
			background-color: $white;

			&:hover {
				background-color: $grey;
			}
		
		}

	}
	
}
.input {

	background-color: $input-background-color;
	border-radius: $input-border-radius;
	padding: $input-padding;
	outline: $input-outline;
	margin-bottom: $input-margin;
	width: $input-width;
	border: $input-border;
		
		&--error {
			border-color: $red !important;
		}
		
		&--hidden {
			@include hidden;
		}

}
.label {
	display: block;
	margin-bottom: 4px;
}
.radio {
	
	&__option {
		display: inline-flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		margin-right: 30px;
		margin-bottom: 15px;
	}

	&__option-wrapper:last-child {
		margin-right: 0px;
	}

	&__input {
		width: 25px;
    height: 25px;
		background-color: $input-background-color;
    border-radius: 50px;
    appearance: none;
    vertical-align: bottom;
    margin-right: 16px;
    background-image: url('/media/icons/form-elements/radio-bullet.svg');
    background-size: 0px;
    background-position: center;
    transition: background-size 0.1s ease;
    background-repeat: no-repeat;
		border: $input-border;

		&:checked {
			background-size: 17px;
		}

	}

	&__label {
		margin-top: 5px;
	}

}
.range {

	&__label {
		margin-bottom: 12px;
	}

	&__input[type="range"] {
		margin: auto;
		-webkit-appearance: none;
		position: relative;
		height: 4px;
		width: 100%;
		cursor: pointer;
		border-radius: 5px;
		margin-bottom: 32px;
	}

	::-webkit-slider-runnable-track {
		height: 8px;
		border-radius: 0;
	}

	::-webkit-slider-thumb {
		-webkit-appearance: none;
		width: 16px;
		height: 16px;
		border-radius: 50%;
		background: $black;
		position: relative;
		top: -4px;

		&:active {
			cursor: grabbing;
		}

	}

	::-moz-range-track {
		height: 8px;
		border-radius: 0;
	}
	
	::-moz-range-thumb {
		width: 16px;
		height: 16px;
		border-radius: 50%;
		background: $black;
		position: relative;
		top: -4px;
	}


}

.textarea {

	background-color: $input-background-color;
	border-radius: $input-border-radius;
	padding: $input-padding;
	outline: $input-outline;
	margin: $input-margin;
	width: $input-width;
	border: $input-border;
	resize: none;	

		&--error {
			border-color: $red !important;
		}
		
		&--hidden {
			@include hidden;
		}

}
.link-navigation {
	
	text-decoration:  none;
	color: $black;

	&--green { color: $green; }
	&--white { color: $white; }
	&--black { color: $black; }
	
}
.link {
	
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	text-decoration:  none;
	
	&:hover {
		.link__arrow {
			margin-left: 6px;
			display: block;
		}
	
	}
	
	&__text {
		margin-right: 8px;
		font-family: 'Roboto-Regular';
	}

	&__arrow  {
		width: 16px;
		height: 16px;
		object-fit: contain;
		transition: all 0.1s ease;
	}

}